const merchant = 'Jibrel can provide merchants with a simple easy to use payment gateway that can accept payments in any currency and pay-out in the local currency. Without incurring exchange or transfer fees.\n\nOnce the network is established, merchants will be able to set-up currency agnostic payment gateways using jibrel’s user-friendly libraries and API.'
export default merchant



