const decentralize = 'In the short to medium term, off-chain activities will be needed to undertake the necessary legal and financial due diligence to convert physical assets to digital assets. In addition, the BOD members will be needed to oversee the JDB to ensure full transparency and regulatory compliance.\n\nIn the longer-term, it is expected that regulation will evolve to facilitate on-chain verification of asset ownership, and a result, enable Jibrel to become a decentralized autonomous organization.'

export default decentralize



