const infrastructure = 'Critical data, such as user balances and transactions, will be stored on the blockchain while all other data is hosted on development servers.\n\nSeveral infrastructural components, tools and frameworks have already been developed to enable the rapid development of decentralized applications. Jibrel will need to develop similar developer components, tools and frameworks to enable the widespread adoption and distribution of CryDRs.\n\nInfrastructure will be required across two main dimensions, on-chain APIs and off-chain APIs / Utils.'
export default infrastructure



