const moneymark = 'Stable low-yield assets are jibrel’s core offering, crypto-investors will be able to purchase tokens tethered to US Treasury Bills and Zero-Coupon Certificates of Deposits. For the first iteration of the jibrel network, all money market instruments will incorporate an automatic rollover or accrual mechanism. Meaning, fiat received from matured investments, will be automatically redeployed in similar assets. Similarly, dividend or interest will be accrued until the underlying asset matures or is sold. In future versions, money market instruments will be configurable.'
export default moneymark



