const crydrcontract = 'Each asset registered with the JDB will have a CryDR issued in the form of a smart contract. CryDR smart contracts will be ERC-20 compliant. Forwarding CryDRs between user accounts is similar to forwarding other ERC-20 tokens between wallets.'
export default crydrcontract



